    user-select: none;
}
h1 {
    text-align: center;
}

.content {
    padding: 5rem 10rem;
    display: flex;
    justify-content: space-between;
    position: relative;
}

.container {
    display: grid;
    grid-template-columns: repeat(10, 50px);
    grid-template-rows: repeat(10, 50px);
}

.container > div {
    border: 1px solid black;
}

.player {
    display: flex;
    gap: 30px;
}

.ships {
    height: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.my-ships {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 20px;
    border: none;
    height: 400px;
    padding-top: 30px;
}

.ship {
    background-color: black;
    border: none;
}

.toggle-button {
    position: absolute;
    bottom: 700px;
    left: 700px;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,iBAAiB;AACrB;AACA;IACI,kBAAkB;AACtB;;AAEA;IACI,mBAAmB;IACnB,aAAa;IACb,8BAA8B;IAC9B,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,uCAAuC;IACvC,oCAAoC;AACxC;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,SAAS;AACb;;AAEA;IACI,aAAa;IACb,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,eAAe;IACf,SAAS;IACT,YAAY;IACZ,aAAa;IACb,iBAAiB;AACrB;;AAEA;IACI,uBAAuB;IACvB,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,WAAW;AACf","sourcesContent":["* {\n    user-select: none;\n}\nh1 {\n    text-align: center;\n}\n\n.content {\n    padding: 5rem 10rem;\n    display: flex;\n    justify-content: space-between;\n    position: relative;\n}\n\n.container {\n    display: grid;\n    grid-template-columns: repeat(10, 50px);\n    grid-template-rows: repeat(10, 50px);\n}\n\n.container > div {\n    border: 1px solid black;\n}\n\n.player {\n    display: flex;\n    gap: 30px;\n}\n\n.ships {\n    height: 500px;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n}\n\n.my-ships {\n    display: flex;\n    flex-direction: column;\n    flex-wrap: wrap;\n    gap: 20px;\n    border: none;\n    height: 400px;\n    padding-top: 30px;\n}\n\n.ship {\n    background-color: black;\n    border: none;\n}\n\n.toggle-button {\n    position: absolute;\n    bottom: 700px;\n    left: 700px;\n}"],"sourceRoot":""}]);
